html, body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background-color: #000;
  color: #fff;
}
body.bg-black {
  background-color: #000;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  color: #fff;
}

.player-app {
  position: relative;
  margin: auto;
  overflow: hidden;
}
.player-app .pixi-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 0;
}
.player-app .pixi-wrapper canvas {
  width: 100%;
  height: 100%;
}
.player-app .countdown {
  position: absolute;
  right: 0.8em;
  bottom: 0.6em;
  box-sizing: content-box;
  font-size: 2em;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: black;
  background-color: white;
  border: 6px solid #000;
  text-align: center;
  width: 10%;
  height: 2em;
  line-height: 2em;
}
.player-app .storyline-bar {
  display: block;
  position: absolute;
  width: 100%;
  height: 10%;
  top: 0;
  background-color: #ffe34a;
  border-bottom: 0.75em solid black;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
}
.player-app .storyline-bar .decision-label {
  display: inline-block;
  padding: 0.3em 0.75em;
  font-size: 1.7em;
  font-weight: bold;
  color: black;
}
.player-app .storyline-bar .countdown {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 2em;
  color: black;
  background-color: white;
  text-align: center;
  width: 10%;
  height: 100%;
  line-height: 1.85em;
}
.map-app {
  position: relative;
  margin: auto;
  overflow: hidden;
}
.map-app .pixi-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 0;
}
.map-app .pixi-wrapper canvas {
  width: 100%;
  height: 100%;
}
.map-app .countdown {
  position: absolute;
  right: 0.8em;
  bottom: 0.6em;
  box-sizing: content-box;
  font-size: 2em;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: black;
  background-color: white;
  border: 6px solid #000;
  text-align: center;
  width: 10%;
  height: 2em;
  line-height: 2em;
}
.map-app .storyline-bar {
  display: block;
  position: absolute;
  width: 100%;
  height: 10%;
  top: 0;
  background-color: #ffe34a;
  border-bottom: 0.75em solid black;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
}
.map-app .storyline-bar .decision-label {
  display: inline-block;
  padding: 0.3em 0.75em;
  font-size: 1.7em;
  font-weight: bold;
  color: black;
}
.map-app .storyline-bar .countdown {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 2em;
  color: black;
  background-color: white;
  text-align: center;
  width: 10%;
  height: 100%;
  line-height: 1.85em;
}

@keyframes popIn {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.dialogue-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  /* Animate the background color from white to yellow */
}
.dialogue-overlay .balloon {
  display: none;
  position: absolute;
  left: 0.6em;
  width: calc(100% - 1.2em);
  height: calc(33% - 1.2em);
  background-color: #fff;
  color: #000;
  border-radius: 1em;
  border: 0.5em solid #000;
  margin: 0;
  padding: 0.6em 1em;
  font-size: 1.5em;
}
.dialogue-overlay .balloon .title {
  font-size: 0.85em;
  font-weight: bold;
  margin-top: -0.5em;
  margin-bottom: -0.1em;
}
.dialogue-overlay .balloon.top {
  top: 0.6em;
}
.dialogue-overlay .balloon.bottom {
  bottom: 0.6em;
}
.dialogue-overlay .balloon.visible {
  display: block;
  animation: popIn 0.25s ease-in-out;
}
.dialogue-overlay .balloon.fading {
  animation: fadeOut 0.25s ease-in-out forwards;
}
@keyframes strikeDown {
  0% {
    transform: translateY(0.25em);
  }
  45% {
    transform: translateY(-0.15em);
  }
  90% {
    transform: translateY(0.25em);
  }
  100% {
    transform: translateY(0.25em);
  }
}
.dialogue-overlay .balloon-speech.press-to-continue:after {
  content: " ";
  display: block;
  position: absolute;
  bottom: -1.15em;
  left: 50%;
  width: 2em;
  height: 2em;
  margin-left: -1em;
  background-image: url("../../static/icons/arrow-triangle-down.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  animation: strikeDown 0.75s ease-in-out infinite;
}
@keyframes flash {
  0% {
    background-color: #fff;
  }
  100% {
    background-color: #ffe34b;
  }
}
.dialogue-overlay .response-option {
  padding-left: 2em;
}
.dialogue-overlay .response-option.selected:before {
  content: " ";
  display: block;
  position: absolute;
  width: 2em;
  height: 2em;
  margin-left: -2em;
  margin-top: -0.2em;
  background-image: url("../../static/openmoji/backhand-index-pointing-right.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.dialogue-overlay .response-option.selected .text {
  background-color: #fff;
  animation: flash 0.5s ease-out forwards;
}
.dialogue-overlay .response-option .text {
  padding: 0 0.1em;
}

.speech-text span {
  opacity: 0;
}
.speech-text span.revealed {
  opacity: 1;
}
.speech-text .break {
  display: block;
  margin-bottom: 0.5em;
}

.title-overlay {
  opacity: 0;
  position: absolute;
  pointer-events: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  /* Opacity pulse animation for the start text */
}
.title-overlay.visible {
  opacity: 1;
}
.title-overlay .logo {
  display: block;
  position: absolute;
  width: 50%;
  height: 32%;
  top: 10%;
  left: 25%;
  background-image: url("../../static/logo/logo-two-lines.svg");
  background-size: 90%;
  background-repeat: no-repeat;
  background-position: center;
  font-size: 0;
  background-color: white;
}
@keyframes pulse {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.title-overlay .press-start {
  position: absolute;
  bottom: 13%;
  width: 100%;
  height: auto;
  text-align: center;
}
.title-overlay .press-start .frame {
  display: inline-block;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: white;
  font-size: 2em;
  background-color: #000;
  padding: 0.2em 1em;
}
.title-overlay .press-start .text {
  display: inline;
  animation: pulse 1.5s infinite ease-in-out;
}

.player-1 .title-overlay .logo {
  background-color: #ffd75a;
}

.player-2 .title-overlay .logo {
  background-color: #6ff2e0;
}

.player-3 .title-overlay .logo {
  background-color: #d9b1ff;
}

.player-4 .title-overlay .logo {
  background-color: #a4faff;
}

.player-5 .title-overlay .logo {
  background-color: #d9ff8c;
}

.player-6 .title-overlay .logo {
  background-color: #ff8c8c;
}

.text-screen {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-weight: bold;
  font-size: 2em;
  background-color: #000;
  color: #fff;
  transition: opacity 0.5s ease-in-out;
}
.text-screen.visible {
  opacity: 1;
}
.text-screen .text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes fallIn {
  0% {
    transform: translate(100%, -100%) rotate(58deg);
  }
  100% {
    transform: none;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.decision-screen-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}
.decision-screen-wrapper .decision-screen {
  transform: translate(100%, -100%) rotate(58deg);
}
.decision-screen-wrapper .decision-screen-icon {
  opacity: 0;
}
.decision-screen-wrapper.visible .decision-screen {
  animation: fallIn 0.5s ease-out forwards;
}
.decision-screen-wrapper.visible .decision-screen-icon {
  animation: fadeIn 0.5s 1.5s ease-out forwards;
}

.decision-screen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffe34a;
  color: black;
  text-align: center;
  padding: 2.5em 5em;
}
.decision-screen .decision-screen-title {
  font-size: 2em;
  color: black;
}
.decision-screen .decision-screen-text {
  font-size: 1.6em;
}
.decision-screen .decision-screen-icon {
  width: 100%;
  height: 12em;
  margin: auto;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}
@keyframes pulse {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.decision-screen .waiting-text {
  margin-top: 2em;
  font-size: 1.5em;
  font-weight: bold;
  display: inline-block;
  padding: 0.25em 0.9em;
}
.decision-screen .waiting-text .text {
  animation: pulse 1.5s infinite ease-in-out;
}

.decision-screen-wrapper .citizensEmpowered .decision-screen-icon {
  background-image: url("../../static/openmoji/decisions/empowerment.svg");
  margin-top: 0.5em;
}
.decision-screen-wrapper .citizensHappy .decision-screen-icon {
  background-image: url("../../static/openmoji/decisions/happy.svg");
}
.decision-screen-wrapper .citizensNeutral .decision-screen-icon {
  background-image: url("../../static/openmoji/decisions/neutral.svg");
}
.decision-screen-wrapper .citizensSad .decision-screen-icon {
  background-image: url("../../static/openmoji/decisions/sad.svg");
}
.decision-screen-wrapper .citizensAngry .decision-screen-icon {
  background-image: url("../../static/openmoji/decisions/angry.svg");
}

@keyframes score {
  0% {
    opacity: 0;
    width: 20%;
    height: 20%;
  }
  50% {
    opacity: 1;
    width: 35%;
    height: 35%;
  }
  80% {
    opacity: 1;
    width: 35%;
    height: 35%;
  }
  100% {
    opacity: 0;
    width: 35%;
    height: 35%;
  }
}
.scoring-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.scoring-overlay .achievement {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20%;
  height: 20%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  animation: score 2s ease-out forwards;
}
.scoring-overlay .achievement-empathy {
  background-image: url("../../static/icons/stats-empathy.svg");
}
.scoring-overlay .achievement-empowerment {
  background-image: url("../../static/icons/stats-empowerment.svg");
}
.scoring-overlay .achievement-equity {
  background-image: url("../../static/icons/stats-equity.svg");
}
.scoring-overlay .achievement-information {
  background-image: url("../../static/icons/stats-information.svg");
}

.quest-overlay {
  position: absolute;
  left: 0.8em;
  bottom: 0.8em;
  width: calc(90% - 3.2em);
  border: 6px solid #000;
  background-color: #ffeb78;
  color: #000;
  margin: 0;
  padding: 0.6em 1em;
  font-size: 1.5em;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-style: normal;
  transform: translateY(calc(100% + 0.8em));
  transition: transform 0.3s ease-in-out 0s;
}
.quest-overlay.visible {
  transform: translateY(0);
  transition-delay: 0.3s;
}
.quest-overlay .counter {
  margin: 0 -0.2em;
}
.quest-overlay .counter .counter-item {
  display: inline-block;
  width: 1em;
  height: 1em;
  border: 0.15em solid #000;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.5490196078);
  margin: 0 0.1em;
}
@keyframes activate {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
.quest-overlay .counter .counter-item.active {
  background-color: #000;
  animation: activate 0.5s ease-in-out;
}

.fatal-error {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(92, 6, 6, 0.85);
  background-image: url("../../static/fa/exclamation-triangle-solid.svg");
  background-size: 100px 100px;
  background-repeat: no-repeat;
  background-position: center 50px;
  padding-top: 160px;
}
.fatal-error .fatal-error-text {
  color: white;
  font-size: 20px;
  text-align: center;
  top: 100px;
  left: 50%;
  width: 600px;
  margin: auto;
  margin-bottom: 0.5em;
  padding-left: 15px;
  padding-right: 15px;
}
.fatal-error .fatal-error-details {
  color: white;
  font-size: 16px;
  text-align: center;
  width: 600px;
  margin: auto;
  padding-left: 15px;
  padding-right: 15px;
}

html.with-fatal-error {
  width: 100%;
  height: 100%;
}
html.with-fatal-error body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.connection-state-view {
  display: none;
  pointer-events: none;
  position: absolute;
  width: 300px;
  padding-top: 135px;
  top: 50%;
  left: 50%;
  margin-top: -150px;
  margin-left: -150px;
  color: black;
  font-size: 20px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
}
.connection-state-view.visible {
  display: block;
}
.connection-state-view .connection-state-view-icon {
  display: block;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 25px;
  left: 100px;
}
.connection-state-view .connection-state-view-error {
  font-weight: bold;
}
.connection-state-view .connection-state-view-status {
  font-size: 0.85em;
  margin-bottom: 1.2em;
}